body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  background-color: #05021c; }

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto; }

a {
  text-decoration: none;
  color: #fefdff; }

p {
  color: #fefdff; }

h1,
h2,
h3 {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  color: #fefdff; }

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem; }

.contenido-centrado {
  max-width: 800px; }

.content-header {
  display: flex;
  align-items: center;
  flex-direction: column; }
  @media (min-width: 768px) {
    .content-header {
      flex-direction: row;
      justify-content: space-between; } }
  .content-header .nav {
    padding-top: 4rem; }
    .content-header .nav a {
      display: flex;
      padding-bottom: 1rem;
      font-size: 2rem;
      padding-right: 2rem;
      font-weight: 300; }
      @media (min-width: 768px) {
        .content-header .nav a {
          display: inline; } }
      .content-header .nav a:hover {
        color: #6129f3;
        transition: 0.3s; }
  .content-header .logo {
    color: #612af4;
    align-items: center center;
    margin-top: 2.5rem; }
    .content-header .logo img {
      width: 15rem;
      background-size: cover;
      max-height: 10rem; }

@media (min-width: 768px) {
  .header {
    display: grid;
    gap: 2.5rem;
    grid-template-columns: repeat(2, 1fr);
    margin-top: 2.5rem; } }

.header .contenido-header {
  font-size: 2rem;
  padding-top: 5rem;
  text-align: center;
  width: 90%; }
  @media (min-width: 768px) {
    .header .contenido-header {
      margin-top: 5rem; } }

.header span {
  color: #6129f3;
  font-weight: 700;
  font-size: 3rem; }

.header h1 {
  font-weight: 900; }

.header h2 {
  font-weight: 700; }

.contendor-skills h2 {
  color: #fefdff;
  font-weight: 700;
  text-align: center;
  font-size: 3rem;
  width: 100%;
  background-color: #552bd4;
  border-radius: 6px;
  margin-bottom: 8rem;
  padding: 0.5rem; }

.contendor-skills .skills {
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(2, 1fr);
  justify-items: center; }
  @media (min-width: 768px) {
    .contendor-skills .skills {
      display: grid;
      gap: 5rem;
      grid-template-columns: repeat(4, 1fr); } }
  .contendor-skills .skills img {
    width: 95px;
    height: 95px; }
    .contendor-skills .skills img:hover {
      -webkit-transform: scale(1.5);
      transform: scale(1.5);
      transition: 0.4s;
      filter: drop-shadow(0 0 30px #612af4); }
    @media (min-width: 768px) {
      .contendor-skills .skills img {
        width: 144px;
        height: 144px; } }

.footer {
  display: flex;
  align-items: center;
  background-color: #552bd4;
  justify-content: space-evenly;
  margin-top: 5rem; }
  .footer a i {
    color: #fefdff;
    font-size: 3rem;
    padding-left: 1rem; }
    .footer a i:hover {
      color: #000;
      transition: 0.3s; }

.portfolio {
  display: grid;
  justify-items: center; }
  @media (min-width: 768px) {
    .portfolio {
      grid-template-columns: repeat(3, 1fr);
      grid-gap: 2rem; } }
  .portfolio .porfolio-item {
    width: 95%;
    margin: 2rem 2rem;
    text-align: center;
    position: relative;
    border-radius: 1.5rem; }
  .portfolio img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 1.5rem; }
  .portfolio .card-info {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 1rem;
    opacity: 0;
    border-radius: 1.5rem;
    padding-top: 4rem; }
    .portfolio .card-info:hover {
      opacity: 1;
      background-color: rgba(0, 0, 0, 0.7);
      transition: all 0.5s ease-in-out;
      cursor: pointer; }
    .portfolio .card-info h3 {
      font-weight: 700; }
