.portfolio {
  display: grid;
  justify-items: center;

  @include tablet {
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2rem;
  }

  .porfolio-item {
    width: 95%;
    margin: 2rem 2rem;
    text-align: center;
    position: relative;
    border-radius: 1.5rem;
  }
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 1.5rem;
  }
  .card-info {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 1rem;
    opacity: 0;
    border-radius: 1.5rem;
    padding-top: 4rem;

    &:hover {
      opacity: 1;
      background-color: rgba($color: #000000, $alpha: 0.7);
      transition: all 0.5s ease-in-out;
      cursor: pointer;
    }
    h3 {
      font-weight: $bold;
    }
  }
}
