.contendor-skills {
  h2 {
    color: $blanco;
    font-weight: $bold;
    text-align: center;
    font-size: 3rem;
    width: 100%;
    background-color: $secundario;
    border-radius: 6px;
    margin-bottom: 8rem;
    padding: 0.5rem;
  }
  .skills {
    @include grid(2, $separacion);
    justify-items: center;
    @include tablet {
      @include grid(4, $separacion) {
      }
    }
    img {
      width: 95px;
      height: 95px;
      &:hover {
        -webkit-transform: scale(1.5);
        transform: scale(1.5);
        transition: 0.4s;
        filter: drop-shadow(0 0 30px #612af4);
      }
      @include tablet {
        width: 144px;
        height: 144px;
      }
    }
  }
}
