.content-header {
  display: flex;
  align-items: center;
  flex-direction: column;
  @include tablet {
    flex-direction: row;
    justify-content: space-between;
  }
  .nav {
    padding-top: 4rem;
    a {
      display: flex;
      padding-bottom: 1rem;
      font-size: 2rem;
      padding-right: 2rem;
      font-weight: $delgada;
      @include tablet {
        display: inline;
      }
      &:hover {
        color: $primario;
        transition: 0.3s;
      }
    }
  }

  .logo {
    color: $terceario;
    align-items: center center;
    margin-top: $separacion/2;

    img {
      width: 15rem;
      background-size: cover;
      max-height: 10rem;
    }
  }
}

.header {
  @include tablet {
    @include grid(2, $separacion/2);
    margin-top: $separacion/2;
  }

  .contenido-header {
    font-size: 2rem;
    padding-top: $separacion;
    text-align: center;
    width: 90%;

    @include tablet {
      margin-top: 5rem;
    }
  }
  span {
    color: $primario;
    font-weight: $bold;
    font-size: 3rem;
  }
  h1 {
    font-weight: $black;
  }
  h2 {
    font-weight: $bold;
  }
}
