.footer {
  display: flex;
  align-items: center;
  background-color: $secundario;
  justify-content: space-evenly;
  margin-top: $separacion;

  a {
    i {
      color: $blanco;
      font-size: 3rem;
      padding-left: $separacion/5;
      &:hover {
        color: $negro;
        transition: 0.3s;
      }
    }
  }
}
