html {
  font-size: 62.5%;
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
body {
  font-family: $fuente_principal;
  font-size: 1.6rem;
  background-color: $fondo;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: $blanco;
}
p {
  color: $blanco;
}
h1,
h2,
h3 {
  font-family: $fuente_principal;
  font-weight: 300;
  color: $blanco;
}
